#album-headline {
  font-size: 48px;
  /* text-shadow: 0 0 10px rgba(0, 0, 0, 0.5); */
  text-align: center;
  color: black;
}
  
#album-sub-headline {
  font-size: 36px;
  color: black;
  text-align: center;
}

#album-headline-plain-text {
  font-size: 24px;
  color: black;
  margin-bottom: .25em;
  text-align: center;
}

.album-patreon-link {
  text-decoration: none;
  cursor: url('../../assets/cursor/sym57.cur'), auto;
}


