@media screen and (max-width: 900px) {
  #album-headline {
    font-size: 48px;
  }
    
  #album-sub-headline {
    font-size: 36px;
  }
  
  #album-headline-plain-text {
    font-size: 24px;
  }

}

@media screen and (max-width: 600px) {

  
}

@media screen and (max-width: 484px) {
  #album-headline {
    font-size: 42px;
  }
    
  #album-sub-headline {
    font-size: 34px;
  }
  
  #album-headline-plain-text {
    font-size: 24px;
  }
  
}


@media screen and (max-width: 420px) {
  #album-headline {
    font-size: 36px;
  }
    
  #album-sub-headline {
    font-size: 28px;
  }
  
  #album-headline-plain-text {
    font-size: 24px;
  }

  
}

@media screen and (max-width: 400px) {

  
}

@media screen and (max-width: 300px) {
  #album-headline {
    font-size: 30px;
  }
    
  #album-sub-headline {
    font-size: 24px;
  }
  
  #album-headline-plain-text {
    font-size: 20px;
  }
}